import service from "auth/FetchInterceptor";
import API_CONSTANTS from "constants/ApiConstant";
import Utils from "utils";
import moment from "moment";
import { DATE_FORMAT_DD_MM_YYYY } from "constants/DateConstant";
import { HEADERS_NAME_FOR_EXPORT } from "constants/CommonConstants";

const dashboardReportService = {};
dashboardReportService.getReport = async body => {
  const { path, method } = API_CONSTANTS.GET_DASHBOARD_RECORDS;
  try {
    const response = await service({
      url: path,
      method,
      data: body,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response.records;
  } catch (e) {
    console.error("Error in getting the employee list.", e);
  }
};

